<script>
   /***************************************************************/
   /* PlotIntervals                                               */
   /* --------------------                                        */
   /* shows intervals and illustrates selection process           */
   /***************************************************************/

   import { Rectangles, XAxis, YAxis, Box } from 'svelte-plots-basic/2d';
   import DatasetsPlotSeries from './DatasetsPlotSeries.svelte';
   import Axes from './Axes.svelte';


   // inputs
   let {props } = $props();

   // file name for the plot
   const fileName = 'ddsimca-love-intervals';

   // get application state from properties
   const s = $derived(props.s)


   /**
    * Computes limits for x-axis.
    *
    * @param {Object} datasets - JSON with datasets.
    */
   function getLimX(calset) {
      if (!calset) return [0, 1];
      const mn = calset.varvalues[0];
      const mx = calset.varvalues[calset.varvalues.length - 1];
      const dx = mx - mn;
      return [mn - dx * 0.10, mx + dx * 0.025];
   }


   // reactive expressions for title, y-axis label and limits
   const title = $derived.by(() => {
      if (!s.outcomes) return 'Data and intervals';
      if (s.outcomes.status === 'running') return 'Calculation in progress...';
   });

   const xLabel = $derived(s.calset.varAxisTitle);
   const rectTop = $derived(s.varselparams ? Array(s.varselparams.nInt).fill(10) : null);
   const rectHeight = $derived(s.varselparams ? Array(s.varselparams.nInt).fill(22) : null);

   // series with intervals
   const si = $derived(rectTop && rectHeight ? {
      left: s.varselparams.left,
      width: s.varselparams.width,
      top: rectTop,
      height: rectHeight,
   } : null);

   // series with excluded intervals
   const si_excl = $derived.by(() => {
      if (si && s.intExcluded.length > 0) {
         const n = s.intExcluded.length;
         const left = Array(n);
         const width = Array(n);
         const top = Array(n);
         const height = Array(n);
         for (let i = 0; i < n; i++) {
            left[i] = si.left[s.intExcluded[i]];
            width[i] = si.width[s.intExcluded[i]];
            top[i] = si.top[s.intExcluded[i]];
            height[i] = si.height[s.intExcluded[i]];
         }
         return {left, width, top, height};
      } else {
         return null;
      }
   });

   // series with currently processed interval
   const pr = $derived(s.outcomes && s.outcomes.status === 'running' && si ? {
      left: [si.left[s.outcomes.curInt]],
      width: [si.width[s.outcomes.curInt]],
      top: [2],
      height: [4],
      faceColor: '#fffa0040' // yellowish
   } : null);

   // x-axis limits
   const limX = $derived(getLimX(s.calset));
   const limY = $derived([0, 1.1]);
</script>


{#if limX && limY}
<Axes {fileName} {limX} {limY} {title} >

   <DatasetsPlotSeries calset={s.calset} {limY} />

   {#if si_excl }
   <Rectangles
      lineColor="transparent"
      faceColor="#00000040"
      {...si_excl}
   />
   {/if}

   {#if s.outcomes &&  s.outcomes.status === 'running'}
      {#if pr}
      <Rectangles {...pr}  />
      {/if}
   {/if}

   {#if si  }
   <Rectangles
      lineColor="#00000010"
      faceColor="#00000005"
      {...si}
   />
   {/if}

   <Box />
   <XAxis label={xLabel} />
   <YAxis showGrid={true} />
</Axes>
{/if}