<script>
   /***************************************************************/
   /* OucomesPlot                                                 */
   /* --------------------                                        */
   /* shows plot with intervals and statictics computed for       */
   /* interval models, also lets user to select and exclude       */
   /* a particular interval                                       */
   /***************************************************************/

   import { Rectangles, XAxis, YAxis, Box } from 'svelte-plots-basic/2d';

   import DatasetsPlotSeries from './DatasetsPlotSeries.svelte';
   import OutcomesPlotSeries from './OutcomesPlotSeries.svelte';
   import Axes from './Axes.svelte';


   // inputs
   let {props, params, callbacks } = $props();

   // parameters for saving the plot
   const fileName = 'ddsimca-love-outcomes';

   const s = $derived(props ? props.s : null);
   const fomKeys = $derived(props ? props.fomKeys : null);
   const fomNames = $derived(props ? props.fomNames : null);
   const seriesColors = $derived(props ? props.seriesColors : null);

   let yMin = $state(0);
   let yMax = $state(1);
   let xLabel = $derived(s.calset.varAxisTitle);


   /**
    * Computes limits for x-axis.
    *
    * @param {Object} datasets - JSON with datasets.
    */
   function getLimX(calset) {
      if (!calset) return [0, 1];
      const mn = calset.varvalues[0];
      const mx = calset.varvalues[calset.varvalues.length - 1];
      const dx = mx - mn;
      return [mn - dx * 0.10, mx + dx * 0.025];
   }


   // reactive expressions for title, y-axis label and limits
   const title = $derived.by(() => {
      if (!s.outcomes) return 'Data and intervals';
      if (s.outcomes.status === 'running') return 'Selection in progress...';
      return 'Variable selection outcomes (A = ' + s.nComp + ')';
   });

   const yLabel = $derived.by(() => {
      if (!s.outcomes || s.outcomes.status === 'running') return ' ';
      return (params.fomView === 'diff' ? 'Difference in ' : '') + fomNames[params.fom];
   });


   const rectTop = $derived(s.varselparams ? Array(s.varselparams.nInt).fill(10) : null);
   const rectHeight = $derived(s.varselparams ? Array(s.varselparams.nInt).fill(22) : null);

   const si = $derived(rectTop && rectHeight ? {
      left: s.varselparams.left,
      width: s.varselparams.width,
      top: rectTop,
      height: rectHeight,
   } : null);


   // reactive expression to set coordinates of excluded intervals' rectangles
   const si_excl = $derived.by(() => {
      if (si && s.intExcluded.length > 0) {
         const n = s.intExcluded.length;
         const left = Array(n);
         const width = Array(n);
         const top = Array(n);
         const height = Array(n);
         for (let i = 0; i < n; i++) {
            left[i] = si.left[s.intExcluded[i]];
            width[i] = si.width[s.intExcluded[i]];
            top[i] = si.top[s.intExcluded[i]];
            height[i] = si.height[s.intExcluded[i]];
         }
         return {left, width, top, height};
      } else {
         return null;
      }
   });

   // x-axis limits
   const limX = $derived(getLimX(s.calset));
   const limY = $derived(params.fomView === 'diff' ? [yMin, yMax] : [0, 1.1]);
</script>


{#if params && limX && limY}
<Axes {fileName} {limX} {limY} {title} >

   <DatasetsPlotSeries calset={s.calset} {limY} />


   {#if s.outcomes}
      <OutcomesPlotSeries
         {s}
         {limX}
         {fomKeys}
         {seriesColors}
         fomView={params.fomView}
         fom={params.fom}
         bind:yMin={yMin}
         bind:yMax={yMax}
      />
   {/if}

   {#if si  && s.intSelected >= 0}
   <Rectangles
      left={[si.left[s.intSelected]]}
      width={[si.width[s.intSelected]]}
      top={[si.top[s.intSelected]]}
      height={[si.height[s.intSelected]]}
      lineColor="#00000010"
      faceColor="#ff000010"
   />
   {/if}

   {#if si }
   <Rectangles
      className="series-love-intervals"
      lineColor="#00000010"
      faceColor="#00000005"
      {...si}
      onclick={callbacks.select}
   />
   {/if}

   {#if si_excl }
   <Rectangles
      lineColor="transparent"
      faceColor="#00000040"
      {...si_excl}
   />
   {/if}

   <Box />
   <XAxis label={xLabel} />
   <YAxis label={yLabel} showGrid={true} />
</Axes>
{/if}