<script>
   // local plot components and the plot router
   import PlotIntervals from './PlotIntervals.svelte';
   import PlotOutcomes from './PlotOutcomes.svelte';
   import PlotProgress from './PlotProgress.svelte';
   import PlotStats from './PlotStats.svelte';

   let {props, params, callbacks, selected = $bindable() } = $props();
   const s = $derived(props.s);

   $effect(() => {
      if (selected > -1) props.selectInt(selected);
   });

   $effect(() => {
      if (s.intSelected < 0) callbacks.select(s.intSelected);
   })

</script>

{#if params.plot === 'intervals' && s.outcomes && s.outcomes.status === 'done'}
<PlotOutcomes {props} {params} {callbacks} {selected} />
{:else if params.plot === 'progress' && s.statsProgress}
<PlotProgress {props} {params}  />
{:else if params.plot === 'statistics' }
<PlotStats {props} {params}  />
{:else}
<PlotIntervals {props} {params} />
{/if}
