<script>
   /******************************************************************/
   /* DownloadsComponent                                             */
   /* -------------------------                                      */
   /* lets user download main outcomes                               */
   /******************************************************************/

   import { Container, Button } from 'svelte-controls-basic';
   import { downloadResults, downloadModel } from '../../ddsimca/src/utils';
   import { downloadData, excludeVars, isempty } from '../../shared/utils';


   let {s} = $props()


   /*****************************************/
   /* Component code                        */
   /*****************************************/

   /**
    * Creats a CSV string for table with progress statistics, turn it to Blob and start download process.
    *
    * @param {Object} statsProgress - JSON with statistics for every step.
    * @param {String} sep - character used to separate values.
    */
   function downloadLOVEHistory(statsProgress, sep, dec) {

      let out = ['#', 'excluded interval', 'nPCs', 'sensitivity', 'specificity', 'efficiency', 'selectivity'].join(sep) + '\n';
      const n = statsProgress.length;
      for (let i = 0; i < n; i++) {
         out += [
            i,
            i === 0 ? '-' : statsProgress[i].int,
            statsProgress[i].ncomp,
            statsProgress[i].sens,
            statsProgress[i].spec,
            statsProgress[i].eff,
            statsProgress[i].sel,
         ].join(sep) + '\n';
      }

      // correct decimal separator if neccessary
      if (dec !== '.') {
         out = out.replace(/\./g, dec);
      }

      const blob = new Blob([out], { type: 'text/csv' });
      const url = window.URL.createObjectURL(blob)
      const a = document.createElement('a')
      a.setAttribute('href', url)
      a.setAttribute('download', `ddsimca-love-progress.csv`);
      a.click()
   }

   /**
    * Let user to download the current results
    * @param name
    * @param currentRes
    */
   function downloadLOVEData(dataset, varsExcluded) {

      if (!varsExcluded || varsExcluded.length === 0) {
         downloadData(dataset, `${dataset.fileName}`);
         return;
      }
      downloadData(excludeVars(dataset, varsExcluded), `${dataset.fileName}-reduced`);
   }

   /**
    * Let user to download the current results
    * @param name
    * @param currentRes
    */
   function downloadLOVEResults(name, dataset, currentOutcomes) {
      const model = currentOutcomes.model;
      const simca = currentOutcomes.simca;
      const res = currentOutcomes[name];
      downloadResults(model, simca, res, dataset, name, []);
   }

   /**
    * Let user to download the current results
    * @param name
    * @param currentRes
    */
   function downloadLOVEModel(currentRes) {
      downloadModel(currentRes.model, currentRes.simca, currentRes.simca.ncomp);
   }
</script>

{#if s.varsExcluded && s.varsExcluded.length > 0}

<Container label='Outcomes'>
   <span class="buttons">
   <Button onclick={() => downloadLOVEHistory(s.statsProgress, s.calset.sep, s.calset.dec)} text="Full progress" />
   <Button onclick={() => downloadLOVEModel(s.currentRes)} text="Model" />
   </span>
</Container>
<Container label='Results'>
   <span class="buttons">
   <Button onclick={() => downloadLOVEResults("tar", s.tarset, s.currentRes)} text="Target" />
   {#if !isempty(s.altset)}
   <Button onclick={() => downloadLOVEResults("alt", s.altset, s.currentRes)} text="Alt" />
   {/if}
   </span>
</Container>
<Container label='Reduced data'>
   <span class="buttons">
   <Button onclick={() => downloadLOVEData(s.calset, s.varsExcluded)} text="Train" />
   <Button onclick={() => downloadLOVEData(s.tarset, s.varsExcluded)} text="Target" />
   {#if !isempty(s.altset)}
   <Button onclick={() => downloadLOVEData(s.altset, s.varsExcluded)} text="Alt" />
   {/if}
   </span>
</Container>
{/if}

<style>
   .buttons {
      position: relative;
      box-sizing: border-box;
      display: inline-block;
      flex: 1 1 auto;
      margin: 0;
      padding: 2px 0;
      width: auto;
      border-radius: 2px;
      cursor: default;
      white-space: nowrap;
   }
</style>