<script>
   /**************************************************************/
   /* SelectedIntervalPlot                                       */
   /* --------------------                                       */
   /* shows plot with statistics for selected interval           */
   /**************************************************************/

   import { Vector } from 'mdatools/arrays';
   import { XAxis, YAxis, Box, Lines, Points, Legend } from 'svelte-plots-basic/2d';
   import Axes from './Axes.svelte';

   let {props, params, callbacks } = $props();

   // get the session and other properties
   const s = $derived(props.s);
   const fomKeys = $derived(props ? props.fomKeys : null);
   const fomNames = $derived(props ? props.fomNames : null);
   const seriesColors = $derived(props ? props.seriesColors : null);

   // permanent values
   const fileName = 'ddsimca-love-models';
   const xLabel = 'PCs';
   const title = 'Variable selection outcomes';
   const limY = [0, 1.1];
   const yLabel = $derived(fomNames[params.fom]);

   function getLegendItem(label, series) {
      return {
         label: label,
         line: {
            lineColor: series.lineColor,
         },
         point: {
            lineColor: series.lineColor,
            lineWidth: 1.25,
            faceColor: '#fafafa',
            marker: 1,
         }
      }
   }
   function getLegend(gs, bs, ss) {
      const items = [];

      if (gs) items.push(getLegendItem('global', gs));
      if (bs) items.push(getLegendItem('current', bs));
      if (ss) items.push(getLegendItem('selected ('  + (s.intSelected + 1) + ')', ss));
      return items;
   }

   let nCompMax = $derived(s.statsGlobal ? s.statsGlobal[[fomKeys[0]]].length : 1);
   let limX = $derived([0, nCompMax + 1]);

   // plot series for global model
   let gs = $derived(s.statsGlobal && params.fom ?
      {
         xValues: Vector.seq(1, nCompMax),
         yValues:new Vector(s.statsGlobal[params.fom]),
         lineColor: seriesColors.global
      } : null
   );

   let gsComp = $derived(gs ?
      {
         xValues: [s.nComp],
         yValues: [gs.yValues.v[s.nComp - 1]],
         lineColor: seriesColors.global
      } : null
   );


   // plot series for best model
   let bs = $derived(s.statsCurrent && params.fom?
      {
         xValues: Vector.seq(1, s.statsCurrent[params.fom].length),
         yValues: new Vector(s.statsCurrent[params.fom]),
         lineColor: seriesColors.best
      } : null
   );

   let bsComp = $derived(bs ?
      {
         xValues: [s.nComp],
         yValues: [bs.yValues.v[s.nComp - 1]],
         lineColor: seriesColors.best
      } : null
   );


   // plot series for selected interval model
   let ss = $derived(s.outcomes && params.fom && s.intSelected >= 0 ?
      {
         xValues: Vector.seq(1, s.outcomes[params.fom].val.ncols),
         yValues: s.outcomes[params.fom].val.getrow(s.intSelected + 1),
         lineColor: seriesColors.selected
      } : null
   );

   let ssComp = $derived(ss ?
      {
         xValues: [s.nComp],
         yValues: [ss.yValues.v[s.nComp - 1]],
         lineColor: seriesColors.selected
      } : null
   );

   let legendItems = $derived(getLegend(gs, bs, ss));
</script>


{#if params}
<Axes {fileName} {xLabel} {yLabel} {limX} {limY} {title}>

   {#if gs}
   <Lines  {...gs} />
   <Points {...gs} faceColor='#fafafa'/>
   <Points {...gsComp} faceColor='#fafafa' lineWidth={3}/>
   {/if}

   {#if bs}
   <Lines  {...bs} />
   <Points {...bs} faceColor='#fafafa'/>
   <Points {...bsComp} faceColor='#fafafa' lineWidth={3}/>
   {/if}

   {#if ss}
   <Lines  {...ss} />
   <Points {...ss} faceColor='#fafafa'/>
   <Points {...ssComp} faceColor='#fafafa' lineWidth={3}/>
   {/if}

   <Legend items={legendItems} position='bottomright' />

   <Box />
   <XAxis showGrid={true} label={xLabel} whole={true}/>
   <YAxis showGrid={true} label={yLabel} />
</Axes>
{/if}