<script>
   /**************************************************************/
   /* --------------------                                       */
   /**************************************************************/

   import { Segments, TextLabels, Rectangles } from 'svelte-plots-basic/2d';
   import IntervalLabel from './IntervalLabel.svelte';

   let {s, limX, fom, fomKeys, fomView, seriesColors, yMin = $bindable(), yMax = $bindable()} = $props();

   // coordinates of rectangles to show bar plots with statistics (and global value)
   const psAll = $derived.by(() => {

      if (!fomView || !s.outcomes || s.outcomes.status !== 'done') return {
         min: 0,
         max: 1,
         values: null
      }

      // const values = s.outcomes[fom][fomView];
      // if (s.ncomp > values.ncols) s.ncomp = values.ncols;
      const x = s.varselparams.mid;
      const w = s.varselparams.width;
      const n = x.length;

      let min = 0;
      let max = 0;
      let out = {};
      const bw = fomView === 'diff' ? 0.21 : 0.84;
      const wfactor = fomView === 'diff' ? 1 : 0;
      const bhw = 0.42;
      let nkey = 0;
      for (const key of fomKeys) {
         const y = s.outcomes[key][fomView].getcolumn(s.nComp).v;
         const left = Array(n);
         const top = Array(n);
         const height = Array(n);
         const width = Array(n);

         for (let i = 0; i < n; i++) {
            left[i] = x[i] - bhw * w[i] + nkey * bw * wfactor * w[i],
            width[i] = bw * w[i];
            if (y [i] >= 0) {
               top[i] = y[i];
               height[i] = y[i];
               max = Math.max(max, y[i])
            } else {
               top[i] = 0;
               height[i] = -y[i];
               min = Math.min(min, y[i])
            }
         }

         out[key] = {left, top, height, width, faceColor: '#a8a04060'};
         nkey = nkey + 1;
      }

      return {values: out, min, max }
   });

   const yDiff = $derived(psAll ? (psAll.max - psAll.min) * 0.20 : 0);

   $effect(() => {
      yMin = psAll.min - yDiff;
      yMax = psAll.max + yDiff;
   });


   const psSelAll = $derived.by(() => {

      if (!psAll || !psAll.values || s.intSelected === undefined || s.intSelected === null || s.intSelected < 0) return null;

      const is = s.intSelected;
      const psl = {};
      const tsl = {};

      // coordinates of intervals for interval label
      const ps1 = psAll.values[fomKeys[0]];
      const left = ps1.left[is]
      const width = ps1.width[is]
      let intLeft, intWidth;
      if (fomView === 'diff') {
         intLeft = left - width/2;
         intWidth = width * fomKeys.length + width;
      } else {
         intLeft = left;
         intWidth = width;
      }

      for(const key of fomKeys) {
         const psk = psAll.values[key];

         const left = psk.left[is];
         const width = psk.width[is];
         const top = psk.top[is];
         const height = psk.height[is];
         const value = top === 0 ? -height : height;
         const label = s.varselparams.intLabels[is];

         psl[key] = {
            left:  [left],
            width: [width],
            top: [top],
            height: [height],
         }

         tsl[key] = {
            x: intLeft,
            y: fomView === 'diff' ? yMax - 0.5 * yDiff: 1.02,
            dx: intWidth,
            dy: yDiff,
            label: [value.toFixed(3), label],
            pos: is / s.varselparams.nInt,
            textSize: 0.80,
            faceColor: seriesColors.selected
         }
      }

      return {psl, tsl};
   });

   const globValue = $derived(s.statsGlobal && fom ?
      s.statsGlobal[fom][s.statsGlobal.ncompBest ? (s.statsGlobal.ncompBest - 1) : (s.nComp - 1)] : null);
   const bestValue = $derived(s.statsCurrent && fom && s.statsCurrent.ncompBest ?
      s.statsCurrent[fom][s.statsCurrent.ncompBest ? (s.statsCurrent.ncompBest - 1) : (s.nComp - 1)] : null);

   const ps = $derived(psAll.values && fom ? psAll.values[fom] : null);
   const globXValue = $derived(ps ? (limX[0] + ps.left[0]) * 0.5 : 0);
</script>

{#if psAll}

   {#if fomView === 'diff'}
      <!-- horizontal line at y = 0 -->
      <Segments xStart={[limX[0]]} xEnd={[limX[1]]} yStart={[0]} yEnd={[0]} lineColor='#808080'/>

      <!-- bar plots with difference in statistics for all foms -->
      {#each fomKeys as key}
      <Rectangles {...psAll.values[key]}
         lineWidth={0}
         faceColor={'#a8a040' + (key === fom ? 'e0' : '60')}
      />
      {/each}

      {#if psSelAll}
         {#each fomKeys as key}
           <Rectangles {...psSelAll.psl[key]}
            lineWidth={0}
            faceColor={seriesColors.selected + (key === fom ? '' : '40')}
         />
         {/each}
         <IntervalLabel {...psSelAll.tsl[fom]} />
      {/if}
   {:else}

      <!-- horizontal line at y = statistic for global model -->
      {#if globValue}
      <Segments xStart={[limX[0]]} xEnd={[limX[1]]} yStart={[globValue]} yEnd={[globValue]} lineColor={seriesColors.global}/>
      <TextLabels xValues={[globXValue]} yValues={[globValue]} labels={[globValue.toFixed(3)]} pos={globValue > bestValue ? 3 : 1} textSize={0.85} faceColor={seriesColors.global}/>
      {/if}

      <!-- horizontal line at y = statistic for best model -->
      {#if bestValue}
      <Segments xStart={[limX[0]]} xEnd={[limX[1]]} yStart={[bestValue]} yEnd={[bestValue]} lineType={3} lineColor={seriesColors.best}/>
      <TextLabels xValues={[globXValue]} yValues={[bestValue]} labels={[bestValue.toFixed(3)]}
         pos={globValue > bestValue ? 1 : 3} textSize={0.85} faceColor={seriesColors.best}/>
      {/if}

      <!-- bar plot with statistics for selected fom -->
      <Rectangles {...ps} faceColor='#a8a04080' />;

      {#if psSelAll}
         <Rectangles {...psSelAll.psl[fom]} lineWidth={0} faceColor={seriesColors.selected} />
         <IntervalLabel {...psSelAll.tsl[fom]} />
      {/if}

   {/if}
{/if}