<script>
   /**************************************************************/
   /* DataPlotSeries                                             */
   /* --------------------                                       */
   /* Shows line plot for mean Â± sd for original training set    */
   /**************************************************************/

   import { mean, sd } from 'mdatools/stat';
   import { Vector } from 'mdatools/arrays';
   import { Lines } from 'svelte-plots-basic/2d';


   let { calset, limY } = $props();

   /*****************************************/
   /* Component code                        */
   /*****************************************/

   // constant parameetrs
   const lineColor = '#80808060';

   // compute mean and upper/lower boundaries as mean Â±Â sd
   const xValues = $derived(calset.varvalues);

   const ys = $derived.by(() => {
      if (!calset) return null;
      const n = xValues.length;
      let mn = Number.POSITIVE_INFINITY;
      let mx = Number.NEGATIVE_INFINITY;
      const m = Vector.zeros(n);
      const s = Vector.zeros(n);
      for (let i = 0; i < xValues.length; i++) {
         const x = calset.values.getcolumn(i + 1);
         const lm = mean(x);
         const ls = sd(x);
         const u = lm + ls;
         const l = lm - ls;
         m.v[i] = lm;
         s.v[i] = ls;
         mn = Math.min(mn, l);
         mx = Math.max(mx, u);
      }
      return {m, s, mn, mx, scale: 1 / (mx - mn)};
   });

   const yp = $derived.by(() => {
      if (!ys) return null;

      let limRange = limY[1] - limY[0];
      let minLim = limY[0] + limRange * 0.1;
      let maxLim = limY[1] - limRange * 0.3;
      limRange = maxLim - minLim;

      const n = xValues.length;
      const m = Vector.zeros(n);
      const su = Vector.zeros(n);
      const sb = Vector.zeros(n);

      // precompute scaling factor
      const f = ys.scale * limRange;

      // scale every value in the series
      for (let i = 0; i < n; i++) {
         const m_i = ys.m.v[i];
         const s_i = ys.s.v[i];
         const su_i = m_i + s_i;
         const sb_i = m_i + s_i;

         su.v[i] = (su_i - ys.mn) * f  + minLim;
         sb.v[i] = (sb_i - ys.mn) * f + minLim;
         m.v[i] = (m_i - ys.mn) * f + minLim;
      }

      return {m, su, sb}
   });


</script>


{#if yp}
<Lines {xValues} yValues={yp.m}  {lineColor} lineWidth={2}/>
<Lines {xValues} yValues={yp.sb} {lineColor} lineWidth={1} lineType={3}/>
<Lines {xValues} yValues={yp.su} {lineColor} lineWidth={1} lineType={3}/>
{/if}
