<script>
   /**************************************************************/
   /* ProgressPlot                                               */
   /* --------------------                                       */
   /* shows plot with statistics vs. selection steps             */
   /**************************************************************/

   import { XAxis, YAxis, Box, Lines, Points } from 'svelte-plots-basic/2d';
   import { seq } from '../../shared/utils.js';
   import Axes from './Axes.svelte';

   // inputs
   let {props, params, callbacks } = $props();

   // parameters for saving the plot
   const fileName = 'ddsimca-love-progress';

   const s = $derived(props ? props.s : null);
   const fomKeys = $derived(props ? props.fomKeys : null);
   const fomNames = $derived(props ? props.fomNames : null);
   const seriesColors = $derived(props ? props.seriesColors : null);

   // title, y-axis label and limits
   const title = 'Variable selection progress';
   const xLabel = 'Steps';
   const yLabel = $derived(fomNames[params.fom]);

   // number of steps and x-axis values
   const n = $derived(s.statsProgress ? s.statsProgress.length : 0);
   const xValues = $derived(n > 0 ? seq(0, n - 1) : null);

   // get values for all FOMs
   const psAll = $derived.by(() => {
      if (n < 1) return null;
      const out = {};
      let yMin = 1.1;
      let yMax = 0;
      for (let i = 0; i < n; i++) {
         const spa = s.statsProgress[i];
         for (const key of fomKeys) {
            if (i === 0) out[key] = [];
            const v = spa[key];
            out[key].push(v);
            yMin = Math.min(yMin, v);
            yMax = Math.max(yMax, v);
         }
      }
      return {series: out, min: yMin, max: yMax};
   });

   // select current form, first and last points
   const ps = $derived(psAll ? {xValues: xValues, yValues: psAll.series[params.fom]} : null)
   const psLast = $derived(ps ? {xValues: [n - 1], yValues: [ps.yValues[n - 1]]} : null);
   const psFirst = $derived(ps ? {xValues: [0], yValues: [ps.yValues[0]]} : null);

   // all FOMs for selected interval
   const ssAll = $derived.by(() => {
      if (!ps || !s.outcomes || s.outcomes.status !== 'done' || s.intSelected < 0) return null;
      let ssMin = 1;
      let ssMax = 0;
      let out = {};

      for (const key of fomKeys) {
         const v = s.outcomes[key].val.getcolumn(s.nComp).v[s.intSelected];
         ssMin = Math.min(ssMin, v);
         ssMax = Math.max(ssMax, v);
         out[key] = {
            xValues: [n - 1, n],
            yValues: [psAll.series[key][n - 1], v]
         }
      };

      return {series: out, min: ssMin, max: ssMax}
   });

   // current selected FOM and line to contect with previous step
   const ss = $derived(ssAll ? ssAll.series[params.fom] : null);
   const ssLine = $derived(psLast ? { xValues: [n - 1, n], yValues: [psLast.yValues[0], ss.yValues[1]] } : null);

   // axis limits
   const limX = $derived(n > 0 ? (ss ? [-0.5, n + 0.5] : [-0.5, n - 0.5]) : [0, 1]);
   const limY = $derived.by(() => {
      if (!psAll || !params.zoom) return [0, 1.1];
      const mn = psAll.min;
      const mx = psAll.max;
      if (!ssAll) return [mn - 0.02, mx + 0.02];
      return [Math.min(mn, ssAll.min) - 0.02, Math.max(mx, ssAll.max + 0.02)];
   });
</script>


{#if psAll}
<Axes {fileName} {limX} {limY} {title} >

   {#if ss}

      {#each Object.keys(ssAll.series) as v}
         <Lines {...ssAll.series[v]} lineColor='#d0d0d0' lineType={3} />
         <Points {...ssAll.series[v]} markerSize={0.75} lineColor='#d0d0d0' faceColor='#e0e0e0' />
      {/each}

      <Lines {...ssLine} lineColor={seriesColors.selected} lineType={3}  />
      <Points {...ss} lineColor={seriesColors.selected} faceColor={seriesColors.selected} borderWidth={2} />
   {/if}


   {#if ps}

      {#each Object.keys(psAll.series) as v}
         <Lines {xValues} yValues={psAll.series[v]} lineColor='#d0d0d0'  />
         <Points {xValues} yValues={psAll.series[v]} markerSize={0.75} lineColor='#d0d0d0' faceColor='#e0e0e0' />
      {/each}

      <Lines {...ps} lineColor='#808080' lineWidth={2} />
      <Points {...ps} lineColor='#808080' faceColor='#fafafa' />
      <Points {...psLast} lineColor={seriesColors.best} lineWidth={2} />
      <Points {...psFirst} lineColor={seriesColors.global} lineWidth={2} />
   {/if}


   <Box />
   <XAxis showGrid={true} label={xLabel} whole={true}/>
   <YAxis showGrid={true} label={yLabel} />
</Axes>
{/if}