<script>
   import { getContext } from 'svelte';
   import { checkCoords, transformCoords, transformObjects } from 'svelte-plots-basic/utils';
   import { LABELS_MARGIN, PLOT_FONT_SIZE } from 'svelte-plots-basic/constants';

   let {
	   x,
      y,
      dx = 0,
      dy = 0,
      label,
      pos = 0,
      faceColor = Colors.PRIMARY_TEXT,
      textSize = 1,                        // size of label symbols
   } = $props();

   function getAnchor() {
      if (pos < 0.25) return 'start';
      if (pos > 0.75) return 'end';
      return 'middle';
   }

   function getX(pos) {
      if (pos < 0.25) return x;
      if (pos > 0.75) return x + dx;
      return x + dx/2;
   }

   // array with text anchors for correct positioning
   const textAnchor = $derived(getAnchor(pos));

   // get axes context and compute coordinates
   const axes = getContext('axes');
   const xs = $derived(x ? transformCoords(checkCoords([getX(pos)]), axes.tX()) : null);
   const ys = $derived(y ? transformCoords(checkCoords([y]), axes.tY()) : null);
   const lineHeight = $derived(PLOT_FONT_SIZE[axes.scales().plot])

   // local status
   const isOk = $derived(x && y);
</script>

{#if isOk}
   {#each label as l, i}
   <text x={xs} y={ys - lineHeight * i} text-anchor={textAnchor} fill={faceColor} font-size='{textSize}em'>{@html l}</text>
   {/each}

{/if}
