<script>
   /**************************************************************/
   /* AppPlots component for plots                               */
   /* -------------------------------                            */
   /* show main DD-SIMCA LOVE plot and enables interaction       */
   /* and excluding intervals.                                   */
   /**************************************************************/

   import { isempty } from '../../shared/utils';

   // local plot components and the plot router
   import PlotMain from './PlotMain.svelte';
   import PlotsRouter from '../../shared/plots/PlotsRouter.svelte';

   // import control components
   import { Select, Range, getDefaults, Container, Button, Switch } from 'svelte-controls-basic'

   // inputs
   let {s, setExcluded, setNComp, selectInt, cancelSelection } = $props();

   // status variable for regulating plots
   let inactive = $state(true);

   // names for main figures of merits
   const fomNames = {
      'sens': 'sensitivity',
      'spec': 'specificity',
      'eff': 'efficiency',
      'sel': 'selectivity'
   };

   // their keys
   const fomKeys = Object.keys(fomNames);

   // colors for plot series
   const seriesColors = {
      'global': '#404040',
      'best': '#2679B2',
      'selected': '#D22C2F'
   }

   // controls for outcome plots
   const plotOptions = ['intervals', 'progress', 'statistics'];
   const fomOptions = isempty(s.altset) ? ['sens'] : ['sens', 'spec', 'eff', 'sel'];

   const plotControls = {
      txt: { el: Select, hidden: false, default: 'Run selection', label: 'Run selection'},
      plot: { el: Select, name: 'plot', hidden: false, default: 'intervals', label: 'Show', props: {options: plotOptions}},
      fom: { el: Select, name: 'fom', hidden: false, default: 'sens', label: 'Statistic', props: {options: fomOptions}},
      nComp: { el: Range, name: 'ncomp', hidden: false, default: 1, label: '# PCs', props: {min: 1, max: 1, step: 1, decNum: 0}},
      fomView: {el: Select, name: 'fomview', hidden: false, default: 'val', label: 'Show', props: {options: ['diff', 'val']}},
      zoom: {el: Switch, name: 'zoom', hidden: false, default: false, label: 'Zoom', props: {options: ['off', 'on']}},
   };


   // JSON with plot components shown when new selection is made
   const plots = $derived({
      main: {label: 'Outcomes', scope: 'objects', component: PlotMain, buttons: buttons},
   });


   // JSON with plots controls for plots_active
   const controls = $state({
      main: plotControls,
   })

   // JSON with parameters for plots_active
   let params = $state({
      main: getDefaults(plotControls),
   });


   // effect to change maximum number of components based on current state
   $effect(() => {
      if (s.outcomes && s.outcomes.status === 'done') {
         controls.main.nComp.props.max = s.outcomes.ncomp;
      }
   });


   // hide everything when calculations are in progress
   $effect(() => {
      if ((s.outcomes && s.outcomes.status === 'running')) {
         // if selection is in progress or it has not been started at all
         // a plain plot with data and intervals will be shown
         // hence we hide all other controls
         inactive = true;
         params.main.plot = 'intervals';
         controls.main.txt.hidden = false;
         controls.main.plot.hidden = true;
      } else {
         inactive = false;
         controls.main.txt.hidden = true;
         controls.main.plot.hidden = false;
      }
   });

   // show or not FOM view  selector
   $effect(() => {
      if (params.main.plot === 'intervals' && s.outcomes && s.outcomes.status === 'done') {
         controls.main.fomView.hidden = false;
      } else {
         controls.main.fomView.hidden = true;
      }
   })

   // show or not FOM selector
   $effect(() => {
      if (
         (params.main.plot === 'intervals' && s.outcomes && s.outcomes.status === 'done') // intervals for recently computed models
         || params.main.plot === 'progress' || params.main.plot === 'statistics') {
         controls.main.fom.hidden = false;
      } else {
         controls.main.fom.hidden = true;
      }
   })

   // handle zoom and fomView controls
   $effect(() => {
      if (params.main.plot !== 'progress') {
         controls.main.zoom.hidden = true;
      } else {
         controls.main.zoom.hidden = inactive;
      }
   })

   // handle number of components control
   // hide zoom controller in interval mode
   $effect(() => {
      if (params.main.plot === 'progress' || (!s.outcomes || s.outcomes.status !== 'done')) {
         controls.main.nComp.hidden = true;
      } else {
         controls.main.nComp.hidden = inactive;
      }
   })


   $effect(() => {
      if (s.statsProgress) {
         controls.main.plot = {label: 'Show', hidden: false, el: Select, props: {options: ['intervals', 'progress', 'statistics']}};
      } else {
         controls.main.plot = {label: 'Show', hidden: false, el: Select, props: {options: ['intervals', 'statistics']}};
         if (params.plot === 'progress') params.plots = 'intervals';
      }
   })

   // effect to change current number of components based on use selection (active plots: outcomes)
   $effect(() => {
      setNComp(params.main.nComp);
   });


   // properties to send to plots
   const props = $derived({s, seriesColors, fomKeys, fomNames, selectInt});
</script>

{#snippet buttons(sel)}
   {#if s.intSelected >= 0}
   <Container label="Selected ({s.intSelected + 1})">
      <Button onclick={cancelSelection} text="cancel" />
      <Button onclick={() => setExcluded(s.intSelected)} text="exclude" />
   </Container>
   {/if}
{/snippet}


<PlotsRouter
   {props}
   {plots}
   {controls}
   selobjText = '',
   bind:params={params}
/>
