<script>
   /**************************************************************/
   /* AppContainer for variable selection                        */
   /* -------------------------                                  */
   /* app container for variable selection in SIMCA model        */
   /**************************************************************/

   import { untrack } from 'svelte';
   import { getCurrentRes, getStat } from './utils';
   import { seq, replacer } from '../../shared/utils.js';

   import { Container, Button, ButtonCancel } from 'svelte-controls-basic';

   import DataLoader from      '../../pca/src/shared/DataLoader.svelte';
   import ModelCalculator from '../../pca/src/shared/ModelCalculator.svelte';
   import PCVCalculator from   '../../pca/src/shared/PCVCalculator.svelte';

   import DownloadsComponent from './DownloadsComponent.svelte';
   import ResultTable from './ResultTable.svelte';
   import AppPlots from './AppPlots.svelte';
   import LOVE from './LOVE.svelte';



   // WASM promise and session object from parent app
   let {WASM, backupSession, session = $bindable()} = $props();

   // get part of the session for this tab
   const s = $derived(session['train']);

   // this is needed to set the new current statistics values
   let undoInProgress = $state(false);


   /**
    * Callback: removes the last selected interval from excluded and recalculates everything.
    * @param e - event dispatched by AppPlots component with id of the selected point
    */
   async function undoExcluded() {

      // if number of excluded interval is less than 2 no need to undo
      if (s.intExcluded.length < 2) return;

      // remove the selection juts in case
      s.intSelected = -1;

      // get last excluded interval
      const id = s.intExcluded[s.intExcluded.length - 1];
      if (!(id >= 0)) return;

      // remove last value from stats progress
      s.statsProgress.pop()

      // identify position of excluded variables corresponding to the interval and how many they are
      const left = s.varselparams.intLeftIndices[id];
      const right = s.varselparams.intRightIndices[id];
      const intLen = right === left ? 1 : right - left + 1;

      // remove excluded interval and variables from the list
      s.intExcluded.pop();
      s.varsExcluded = s.varsExcluded.slice(0, s.varsExcluded.length - intLen);

      // adjust number of components in the current best model
      const np = s.statsProgress.length;
      s.nComp = s.statsProgress[np - 1].ncomp;

      // reset selected interval and outcomes
      s.intSelected = -1;
      s.outcomes = null;

      // compute PCA/SIMCA results for the excluded intervals
      undoInProgress = true;

      // update generate current results
      await getCurrentRes(WASM, s);

      // and recompute the current statistics
      const ncompBest = s.currentRes.simca.ncomp;
      const sens = s.currentRes.tar.simca.stat.sensitivity;
      const spec = s.currentRes.alt.simca.stat.specificity;
      const sel = s.currentRes.alt.simca.stat.selectivity;
      const eff = sens.map((v, i) => Math.sqrt(v * spec[i]));

      s.statsCurrent = {
         ncomp: sens.length,
         ncompBest: ncompBest,
         sens: new Float64Array(sens),
         spec: new Float64Array(spec),
         eff: new Float64Array(eff),
         sel: new Float64Array(sel),
      };
   }

   /**
    * Callback: sets the selected interval as excloded
    * @param id - interval index to exclude
    */
   function setExcluded() {

      // get position (index) of the interval to be removed and check its validity
      const id = s.intSelected;
      if (Number.isNaN(id) || id < 0 || id >= s.varselparams.nInt ) return;

      // if it is already excluded do nothing
      if (s.intExcluded.length > 0 && s.intExcluded.includes(id)) return;

      // if this is the first exclusion, save current ncomp as best to global statistics
      if (s.intExcluded.length === 0) {
         s.statsGlobal.ncompBest = s.nComp;
      }

      // save the statics of interval to be excluded as current model statistics
      // in order to compute difference
      s.statsCurrent = {
         ncomp: s.outcomes.nComp,
         ncompBest: s.nComp,
         sens: s.outcomes.sens.val.getrow(id + 1).v,
         spec: s.outcomes.spec.val.getrow(id + 1).v,
         eff: s.outcomes.eff.val.getrow(id + 1).v,
         sel: s.outcomes.sel.val.getrow(id + 1).v,
      };

      // if progress statistics is empty create it and add
      // global model statistics as first record
      if (!s.statsProgress) {
         s.statsProgress = [
            {
               ncomp: s.nComp,
               int: id + 1,
               sens: s.statsGlobal.sens[s.nComp - 1],
               spec: s.statsGlobal.spec[s.nComp - 1],
               eff: s.statsGlobal.eff[s.nComp - 1],
               sel: s.statsGlobal.sel[s.nComp - 1]
            }
         ]
      };

      // and then add current statistics
      s.statsProgress.push({
         ncomp: s.nComp,
         int: id + 1,
         sens: s.statsCurrent.sens[s.nComp - 1],
         spec: s.statsCurrent.spec[s.nComp - 1],
         eff: s.statsCurrent.eff[s.nComp - 1],
         sel: s.statsCurrent.sel[s.nComp - 1],
      });

      // exclude the interval and recompute the variable excluded array
      s.intExcluded.push(id);
      s.varsExcluded = s.varsExcluded.concat(seq(s.varselparams.intLeftIndices[id], s.varselparams.intRightIndices[id]));

      // reset selected interval and outcomes
      s.intSelected = -1;
      s.outcomes = null;

      // compute PCA/SIMCA results for the excluded intervals and selected number of components
      getCurrentRes(WASM, s);
   }

   /**
    * Callback: set user selected number of components.
    * @param nComp - number of components selected by user.
    */
   function setNComp(nComp) {
      s.nComp = nComp;
   }

   /**
    * Callback: marks a selected interval as candidate for exclusion.
    * @param selected - id of the interval selected by user.
    */
   function selectInt(selected) {
      s.intSelected = selected;
   }

   /**
    * Callback: resets arrays with excluded intervals and variables and set current model statistics
    * back to the global model.
    */
   function resetExcluded() {
      s.intExcluded = [];
      s.varsExcluded = [];
      s.statsCurrent = s.statsGlobal ? {
         ncompBest: null,
         ncomp: s.statsGlobal.ncomp,
         sens: s.statsGlobal.sens,
         spec: s.statsGlobal.spec,
         eff: s.statsGlobal.eff,
         sel: s.statsGlobal.sel,
      } : null;
      s.statsProgress = null;
      s.intSelected = -1;
      s.outcomes = null;
      if (s.statsGlobal) {
         s.statsGlobal.ncompBest = null;
      }
   }

   /**
    * Callback: reset selected interval
    */
   function cancelSelection() {
      s.intSelected = -1
   }

   /**
    * Resets alternative class set
    */
   function resetAlt() {
      s.hasAlt = undefined;
      s.altres = null;
      s.altset = null;
   }

   // if calibration set is null, set results and model objects to null as well.
   $effect(() => {
      if (s.calset === null) {
         s.calres = null;
         s.model = null;
      }
   });


   // if model is null, set target set results and dataset to null as well.
   $effect(() => {
      if (s.model === null) {
         s.tarres = null;
         s.tarsetType = null;
         s.tarset = null;
      }
   });


   // if target set is null, set remove alt set, altset results, and other objects to null as well.
   $effect(() => {
      if (s.tarset === null) {
         s.tarres = null;
         s.tarsetType = null;
         s.altres = null;
         s.altset = null;
         s.hasAlt = undefined;
      }
   });

   // reactive expression to reset alternative set results and all outcomes if alternative set was cancelled
   $effect(() => {
      if (s.altset === null) {
         s.intExcluded = [];
         s.varsExcluded = [];
         s.statsCurrent = null;
         s.statsGlobal = null;
         s.statsProgress = null;
         s.intSelected = -1;
         s.outcomes = null;
         s.altres = null;
         s.hasAlt = undefined;
      }
   });

   // if target set and alt set results are updated compute statistics
   // and save them as global model
   $effect(() => {
      if (s.tarres && s.altres && !s.statsGlobal) {
         // initialization
         const stat = getStat(s.model, s.tarres, s.altres, null, s.alpha);
         s.statsGlobal = {
            ncomp: stat.ncomp,
            ncompBest: null,
            sens: stat.sens.val,
            spec: stat.spec.val,
            eff: stat.eff.val,
            sel: stat.sel.val,
         }
         s.statsCurrent = {
            ncomp: s.statsGlobal.ncomp,
            ncompBest: null,              // at this stage not best component is available
            sens: s.statsGlobal.sens,
            spec: s.statsGlobal.spec,
            eff: s.statsGlobal.eff,
            sel: s.statsGlobal.sel,
         };
      }
   });


   // when no outcomes - no selected interval
   $effect(() => {
      if (s.outcomes && s.outcomes.status !== 'done' && s.intSelected >= 0) {
         s.intSelected = -1;
      }
   });

   $effect(() => {
      if (s.hasAlt === false) {
         s.altset = {};
         s.altres = {};
      }
   });

   // effect for triggering the session updates
   $effect(() => {
      if (s.outcomes && s.outcomes.status === 'done') {
         // when calculation of local models is done - save the session
         const sessionStr = untrack(() => JSON.stringify(session, replacer));
         backupSession(sessionStr);
      } else if (!s.outcomes) {
         // or save it either on preliminary stage or when interval has been selected
         const sessionStr = untrack(() => JSON.stringify(session, replacer));
         backupSession(sessionStr, s.model, s.calset, s.tarres, s.altres, s.varsExcluded);
      }
   })
</script>


<div class="app-container">

   <!-- left column with main app controls -->
   <div class="app-controls">
      <div class="data-loader-train">
         <DataLoader
            {WASM}
            title='Training set'
            classMandatory={true}
            classUnique={true}
            bind:dataset={s.calset}
         />
      </div>

      {#if s.calset }
      <div class="model-calculator">
         <ModelCalculator
            {WASM}
            dataset={s.calset}
            bind:model={s.model}
            bind:resobj={s.calres}
         />
      </div>
      {/if}

      {#if s.model && s.calset && s.calres}
         <div class="tarset-loader">

         {#if !s.tarsetType && !s.tarset}
            <Container name="select-tarset-type" label="Target set">
               <Button onclick={() => s.tarsetType = 'file'} text="From file" />
               <Button onclick={() => s.tarsetType = 'pv'} text="PV-set" />
            </Container>

         {:else if s.tarsetType === 'file'}
            {#if !s.tarset}
            <Container label="">
               <h2>Upload target test set</h2>
            </Container>
            {/if}
            <DataLoader
               {WASM}
               model={s.model}
               title='Target test set'
               bind:dataset={s.tarset}
               bind:resobj={s.tarres}
            />

         {:else if s.tarsetType === 'pv'}
            {#if !s.tarset}
            <Container label="">
               <h2>Generate PV-set</h2>
            </Container>
            {/if}
            <PCVCalculator
               {WASM}
               calset={s.calset}
               calres={s.calres}
               model={s.model}
               bind:pvres={s.tarres}
               bind:pvset={s.tarset}
            />
         {/if}
         </div>
      {/if}

      {#if s.tarset && s.tarres}
      <div class="data-loader-alt">
         {#if !s.altset}
            {#if s.hasAlt === undefined}
            <Container className="oops" label="Alternative set">
               <Button text="Skip" onclick={() => s.hasAlt = false} />
               <Button text="From file" onclick={() => s.hasAlt = true} />
            </Container>
            {/if}
         {/if}

         {#if s.hasAlt === true}
            {#if !s.altset}
            <Container label="">
               <h2>Load alternative test set</h2>
            </Container>
            {/if}
            <DataLoader
               {WASM}
               title='Alternative test set'
               model={s.model}
               classMandatory={true}
               classUnique={false}
               bind:dataset={s.altset}
               bind:resobj={s.altres}
            />
         {:else if s.hasAlt === false && s.altset !== null}
            <Container label="">
               <p>No alternative set (rigorous mode)</p>
               <ButtonCancel onclick={resetAlt} />
            </Container>
         {/if}
      </div>
      {/if}

      {#if s.calset && s.altset && s.altres && s.statsGlobal}
      <div class="love-settings">
         <LOVE
            {WASM}
            calset={s.calset}
            tarset={s.tarset}
            altset={s.altset}
            model={s.model}
            intExcluded={s.intExcluded}
            varsExcluded={s.varsExcluded}
            statsCurrent={s.statsCurrent}
            bind:varselparams={s.varselparams}
            bind:outcomes={s.outcomes}
            bind:alpha={s.alpha}
            curNInt={s.nInt}
            {resetExcluded}
            {undoExcluded}
         />
      </div>
      {/if}

      {#if s.statsGlobal || s.statsCurrent }
      <div class="res-table">
         <ResultTable {s} />
      </div>
      {/if}
      {#if s.intExcluded.length > 0 && s.intSelected < 0 && (!s.outcomes || s.outcomes.status !== 'running')}
      <div class="downloads-widget">
         <DownloadsComponent {s} />
      </div>
      {/if}

   </div>

   <!-- right column with help text or with plots and plot controls -->
   <div class="app-content">

      {#if !s.altres || !s.statsGlobal}
         <div class="help-text">
            <p>
               In this app can apply DDSIMCA-LOVE &mdash; interactive method for variable selection for DD-SIMCA classifier. The method can work
               in <em>compliant</em> (with both target class objects and objects from alternative classes) or in rigorous (using only target class objects) mode. After selection is done, you can use the final model and/or rediced datasets in the main <a href="https://mda.tools/ddsimca/">DD-SIMCA app</a>.

            </p>
            {#if !s.calset}
            <p><strong>Step 1. Load training set from a CSV file.</strong></p>
            <p> The file should contain data as numeric values delimited
               by comma (with dot as decimal separator) or by semicolon (with comma as decimal separator). Rows correspond to observations or objects, columns â€” to variables.
            </p>
            <p>
               First column must contain class names for each row. All rows in the training set should belong to the target class, so it is expected that the class names are the same (you will get error message if not).
            </p>
            <p>
               First row can contain variable names (<strong>header</strong>) and first column can contain labels for the objects/observations (<strong>row labels</strong>) â€” just set the corresponding options after selecting the file. In this case it is assumed that column with the class names is located next to the column with row labels. Here is an example:
            </p>
            <div>
               <table>
               <thead>
               <tr>
                  <td>Name</td>
                  <td>Class</td>
                  <td>x1</td>
                  <td>x2</td>
                  <td>x3</td>
               </tr>
               </thead>
               <tbody>
               <tr>
                  <td>O1</td>
                  <td>A</td>
                  <td>3.8</td>
                  <td>1.3</td>
                  <td>13.6</td>
               </tr>
               <tr>
                  <td>O2</td>
                  <td>A</td>
                  <td>11.1</td>
                  <td>12.3</td>
                  <td>3.3</td>
               </tr>
               <tr>
                  <td>O3</td>
                  <td>A</td>
                  <td>1.2</td>
                  <td>2.3</td>
                  <td>4.2</td>
               </tr>
               </tbody>
               </table>
            </div>
            <p></p>
            {/if}

            {#if s.calset && !s.model}
            <p><strong>Step 2. Create a global PCA model</strong></p>
            <p>
               Next step is to create a global PCA model for the calibration set, which will be the basis for the SIMCA model. Use more components/latent variables (PCs) than you expect to be optimal. You will have possibility to set
               optimal number later.
            </p>
            {/if}

            {#if s.model && !s.tarset}
            <p><strong>Step 3. Compute PV-set</strong></p>
            <p>
               Now set necessary parameters for Procrustes cross-validation and click button to compute the PV-set, which will help you to optimize SIMCA model parameters. The parameters are the same as for conventional cross-validation: type of splits and number of segments. It is recommended to use fewer number of segments (e.g. 3, 4 or 5).
            </p>
            {/if}

            {#if s.model && s.tarset && !s.altset}
            <p><strong>Step 4. Load or skip dataset with objects of alternative classes</strong></p>
            <p>

               Finally you can either load test set with alternative classes members from a CSV file or skip it and work only with target class (rigorous mode). In case if you want to load the alternatvie dataset, the file should be organized in the same way as the training set.
            </p>
            <p>
               After this step you will be able to start variable selection procedure.
            </p>
            {/if}
         </div>
      {:else if (s.calset && s.varselparams)}
         {#key s.intExcluded}
         <AppPlots {s} {setExcluded} {setNComp} {selectInt} {cancelSelection} />
         {/key}
      {/if}
   </div>
</div>

