<script>
   /*************************************************************/
   /* Root component for the application                        */
   /*************************************************************/

   import { date2str } from '../../shared/utils';

   import App from '../../shared/components/MainApp.svelte';
   import TrainAppContainer from './AppContainer.svelte';
   import WASM from '../../shared/wasm/dist/pca_wasm.js';


   /* callback to check validity of JSON with session and sanitize it if necessary */
   function checkSession(s) {
      const keys1 = Object.keys(s);
      if (!['date', 'train', 'tab'].every(v => keys1.includes(v))) return false;

      const keys2 = Object.keys(s.train);
      if (!['calset', 'calres', 'altset', 'tarset', 'testres', 'model'].every(v => keys2.includes(v))) return false;

      if (s.train.outcomes && s.train.outcomes.status === 'running') {
         s.train.outcomes = null;
      }
      if (s.train.tarsetType && !s.train.tarset) {
         s.train.tarsetType = null;
      }

      if (s.train.model && !s.train.calres) {
         s.train.model = null;
      }

      return true;
   }

   /* callback to initialize new session */
   function initSession() {
      return {
         date: date2str(new Date()),
         tab: 0,
         train: {

            /* datasets, results and global model  */
            calset: 0,           // calibration/training set (loaded by user)
            model : 0,           // PCA model created based on 'calset'
            calres: 0,           // PCA results for calibration set computed when model is created
            tarsetType: 0,       // type of target test set ('file' or 'pv')
            tarset: 0,           // test set for target class (loaded by user or generated as PV-set)
            hasAlt: undefined,   // will user use alternative set or not
            altset: 0,           // test set for alternative classes (loaded by user)
            testres: 0,           // target set PCA results computed after 'tarset' is loaded or generated

            /* current situation in selection process  */
            intSelected: -1,     // which interval is currently selected as candidate
            intExcluded: [],     // list of infices of already excluded intervals
            varsExcluded: [],    // list of indices of already excluded variables
            varselparams: 0,     // parameters of variable selection ()
            currentRes: 0,       // object with current best results

            /* statistics  */
            statsGlobal: 0,      // JSON with statistics of global model with all variables included
            statsCurrent: 0,     // JSON with statistics of current best model with 'varExcluded' variables
            statsProgress: 0,    // JSON with statistics from each exclusion step

            /* additionl parameters */
            alpha: 5,            // alpha defined by user in LOVE
            nComp: 0,            // number of compinents currently selected by user

            /* objects with selection outcomes */
            outcomes: 0,
            oldInt: 0,
            oldNInt: 0
         },
      }
   }

   // app containers
   const tabs = [
      {name: 'Train', label: 'train', component: TrainAppContainer},
   ]
</script>

<App
   appID="ddsimca-love"
   appTitle="DD-SIMCA with LOVE"
   appShortTitle="DD-SIMCA variable selection"
   appSlogan="Variable selection for DD-SIMCA"
   {WASM}
   {tabs}
   {initSession}
   {checkSession}
>

   {#snippet footer()}
      <p>Current release: <span>1.2.4</span>
      &nbsp; Feedback: <a class="paper-link" href="mailto:help@mda.tools?subject=DDSIMCA-LOVE app">help@mda.tools</a>
      &nbsp; Learn: <a class="paper-link" href="https://dx.doi.org/10.1016/j.aca.2025.344302" download>paper</a>
      | <a class="paper-link" href="/ddsimca-love/Wine.zip">dataset</a>
      | <a href="https://youtu.be/ZqKtHZAEEi4">video lesson</a>
      </p>
   {/snippet}

   {#snippet info()}
   <h2>DD-SIMCA variable selection</h2>
   <p>Authors: <em>Alexey Pomerantsev, Sergey Kucheryavskiy, Oxana Rodionova</em></p>
   <p>
      Interactive web-application for excluding least important variables in DD-SIMCA one class classifier.
      The method is an evolution of known wrapper methods, it provides a tool for interactive variable selection,
      based on careful analysis of the main and secondary performance indicators (sensitivity, specificity, efficiency,
      selectivity), as well as domain knowledge.
   </p>
   <p>Check our open access <a href="https://dx.doi.org/10.1016/j.aca.2025.344302">paper</a> for more details about the method.
   </p>

   <h3>v. 1.2.4</h3>
   <p class="release-info">released 20/01/2026</p>
   <ul>
      <li>Small improvements and updates.</li>
   </ul>

   <h3>v. 1.2.3</h3>
   <p class="release-info">released 14/09/2025</p>
   <ul>
      <li>Small improvements to results downloader.</li>
   </ul>

   <h3>v. 1.2.2</h3>
   <p class="release-info">released 08/09/2025</p>
   <ul>
      <li>Fixed a bug related to wrong diff values after undo.</li>
      <li>Default value for gamma is set to 1% (to match the one in DDSIMCA app).</li>
      <li>Small code improvements.</li>
   </ul>
   {/snippet}
</App>

