<script>
    import { seq } from "../../shared/utils";

   /******************************************************************/
   /* ResultsTable                                                   */
   /* -------------------------                                      */
   /* shows table with SIMCA-LOVE results global and current models  */
   /******************************************************************/

   let { s } = $props();

   const MAX_RECORDS = 5;

   const lastRec = $derived(s.statsProgress ? s.statsProgress.length : 0);
   const firstRec = $derived(lastRec - MAX_RECORDS + 1);
   const items = $derived(seq(firstRec <= 0 ? 0 : firstRec, lastRec - 1));

   const lastRow = $derived(s.outcomes && s.outcomes.status === 'done' && s.intSelected >= 0 ? {
         i: lastRec,
         ncomp: s.nComp,
         int: s.intSelected + 1,
         sens: s.outcomes.sens.val.getcolumn(s.nComp).v[s.intSelected],
         spec: s.outcomes.spec.val.getcolumn(s.nComp).v[s.intSelected],
         eff: s.outcomes.eff.val.getcolumn(s.nComp).v[s.intSelected],
         sel: s.outcomes.sel.val.getcolumn(s.nComp).v[s.intSelected]
      } : null);
</script>


{#if s.statsGlobal && s.nComp}
<table>
   <thead>
   <tr>
      <th>#</th>
      <th>int</th>
      <th>#PCs</th>
      <th>sens</th>
      <th>spec</th>
      <th>eff</th>
      <th>sel</th>
   </tr>
   </thead>
   <tbody>
   {#if !s.statsProgress }
   <tr class="global">
      <td>0</td>
      <td>-</td>
      <td>{s.nComp}</td>
      <td>{s.statsGlobal.sens[s.nComp - 1].toFixed(3)}</td>
      <td>{s.statsGlobal.spec[s.nComp - 1].toFixed(3)}</td>
      <td>{s.statsGlobal.eff[s.nComp - 1].toFixed(3)}</td>
      <td>{s.statsGlobal.sel[s.nComp - 1].toFixed(3)}</td>
   </tr>
   {:else}

   {#if firstRec > 0}
   <tr class="global">
      <td>{0}</td>
      <td>-</td>
      <td>{s.statsProgress[0].ncomp}</td>
      <td>{s.statsProgress[0].sens.toFixed(3)}</td>
      <td>{s.statsProgress[0].spec.toFixed(3)}</td>
      <td>{s.statsProgress[0].eff.toFixed(3)}</td>
      <td>{s.statsProgress[0].sel.toFixed(3)}</td>
   </tr>
   <tr>
      <td>...</td>
      <td>...</td>
      <td>...</td>
      <td>...</td>
      <td>...</td>
      <td>...</td>
      <td>...</td>
   </tr>
   {/if}

   {#each items as i}
   <tr class:global={i==0} class:current={i === s.statsProgress.length - 1}>
      <td>{i}</td>
      <td>{i > 0 ? s.statsProgress[i].int : '-'}</td>
      <td>{s.statsProgress[i].ncomp}</td>
      <td>{s.statsProgress[i].sens.toFixed(3)}</td>
      <td>{s.statsProgress[i].spec.toFixed(3)}</td>
      <td>{s.statsProgress[i].eff.toFixed(3)}</td>
      <td>{s.statsProgress[i].sel.toFixed(3)}</td>
   </tr>
   {/each}

   {/if}

   {#if lastRow }
   <tr class="selected">
      <td>{lastRow.i}</td>
      <td>{lastRow.int}</td>
      <td>{lastRow.ncomp}</td>
      <td>{lastRow.sens.toFixed(3)}</td>
      <td>{lastRow.spec.toFixed(3)}</td>
      <td>{lastRow.eff.toFixed(3)}</td>
      <td>{lastRow.sel.toFixed(3)}</td>
   </tr>
   {/if}
   </tbody>
</table>
{/if}

<style>
   table {
      width: 100%;
      border-collapse: collapse;
      font-size: 0.85em;
      margin-top: 1em;
   }

   tr {
      width: 100%;
   }

   th {
      border-bottom: 1px solid #a0a0a0;
   }

   th, td {
      text-align: right;
      padding: 0.35em 0.5em 0.35em 0.1em;
      color: #404040;
      margin: 0;
   }

   td {
      color: #a0a0a0;
   }


   tr.global td{
      color: #404040;
   }

   tr.current td{
      color: #2679B2;
   }

   tr.selected td{
      color: #D22C2F;
   }
</style>